"use client";

import { ColumnDef } from "@tanstack/react-table";
import { MoreHorizontal, ArrowUpDown } from "lucide-react";

import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { Badge } from "@/components/ui/badge";
import { Client } from "@/data/mock";
import { useAuth } from "@/hooks/use-auth";

const ActionCell = ({ row }: { row: any }) => {
  const { role } = useAuth();
  const client = row.original;

  return (
    <DropdownMenu>
      <DropdownMenuTrigger asChild>
        <Button variant="ghost" className="h-8 w-8 p-0">
          <span className="sr-only">Open menu</span>
          <MoreHorizontal className="h-4 w-4" />
        </Button>
      </DropdownMenuTrigger>
      <DropdownMenuContent align="end">
        <DropdownMenuLabel>Actions</DropdownMenuLabel>
        <DropdownMenuItem onClick={() => navigator.clipboard.writeText(client.id)}>
          Copy Client ID
        </DropdownMenuItem>
        <DropdownMenuSeparator />
        <DropdownMenuItem>Edit</DropdownMenuItem>
        {role === "admin" && (
          <DropdownMenuItem className="text-destructive focus:bg-destructive/20 focus:text-destructive">
            Delete
          </DropdownMenuItem>
        )}
      </DropdownMenuContent>
    </DropdownMenu>
  );
};


export const columns: ColumnDef<Client>[] = [
  {
    accessorKey: "name",
    header: ({ column }) => {
      return (
        <Button
          variant="ghost"
          onClick={() => column.toggleSorting(column.getIsSorted() === "asc")}
        >
          Name
          <ArrowUpDown className="ml-2 h-4 w-4" />
        </Button>
      );
    },
    cell: ({ row }) => (
        <div className="font-medium">{row.getValue("name")}</div>
    ),
  },
  {
    accessorKey: "company",
    header: "Company",
  },
  {
    accessorKey: "project",
    header: "Project",
  },
  {
    accessorKey: "status",
    header: "Status",
    cell: ({ row }) => {
      const status = row.getValue("status") as string;
      const variant: "default" | "secondary" | "destructive" | "outline" =
        status === "Active"
          ? "default"
          : status === "Completed"
          ? "secondary"
          : status === "On Hold"
          ? "outline"
          : "destructive";
      return <Badge variant={variant} className={status === "Active" ? "bg-green-500/80 hover:bg-green-500 text-white" : ""}>{status}</Badge>;
    },
  },
  {
    accessorKey: "cooperationDate",
    header: "Cooperation Date",
    cell: ({ row }) => new Date(row.getValue("cooperationDate")).toLocaleDateString(),
  },
  {
    id: "actions",
    cell: ActionCell,
  },
];
